import { RemoteCore } from "./RemoteCore.js";
import { MBP, Buffer} from 'meta-buffer-pack'

export class Remote extends RemoteCore{
  constructor(url  ) {
    super(url);
    this.connect();
  }

  runChecker(url) {
    // console.log('.')
    if ( this.socket?.readyState !== 1 ) {
      this.connect();
    }
  }

  close() {
    this.socket?.close();
    this.socket = null;
    clearInterval(this.runCheckIntervalID);
    this.runCheckIntervalID = null
  }

  open(url){
    this.connect(url)
  }


  connect(url) {
    if (url) this.serverURL = url;

    if(this.runCheckIntervalID)  clearInterval(this.runCheckIntervalID);
    this.runCheckIntervalID = setInterval(this.runChecker.bind(this), this.runCheckPeriod);
    
    this.socket?.close()
    this.socket = null;
    
    console.log('-- new websocket')
    // Web Browser WebSocket
      this.socket = new WebSocket (this.serverURL );
      this.socket.binaryType = "arraybuffer"
      this.socket.onopen = (e) => {
        this.socket.onmessage = this.onWebSocketMessage.bind(this) ;
        this.emit('open' );
      };

      this.socket.onerror = (e)=>{ 
        this.emit('error', e)
      }

      this.socket.onclose = (e)=>{ 
        this.emit("close" );
      }
  
  } //end connect 

  onWebSocketMessage( event ) {
    this.rxCounter++;
    this.lastTxRxTime = Date.now();
    let buffer;

    // if( event.data instanceof ArrayBuffer ){
    //   //binary frame
    // }else{
    //   //text frame
    // }
    buffer = Buffer.from( event.data )
    this.emit('socket_data', buffer  );
  }

  send(data) {  
    if( this.socket?.readyState === 1 ){
      // console.log('websocket send', data)
      this.socket.send( data )
      this.txCounter++;
      this.lastTxRxTime = Date.now();
    }else{
      console.log('-- send()::socket not open')
    }
  }
 
}




Remote.Buffer = Buffer;
Remote.MBP = MBP;

